<template>
  <div class="addJobItem">
    <div class="content">
      <el-row :gutter="20">
        <el-col :span="12" :offset="6">
          <div class="content-left">
            <el-form ref="entJobInfo" :model="entJobInfo" label-width="120px" :rules="rules">
              <el-form-item label="职位名称" prop="jobName">
                <el-input v-model="entJobInfo.jobName"></el-input>
              </el-form-item>
              <el-form-item label="所属部门" prop="jobDepart">
                <el-input v-model="entJobInfo.jobDepart"></el-input>
              </el-form-item>
              <el-form-item label="职位类别" prop="jobClass">
                <el-select v-model="entJobInfo.jobClass">
                  <el-option v-for="item in options" :key="item" :label="item" :value="item"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="职位性质">
                <el-radio-group v-model="entJobInfo.jobType" text-color="#fff" fill="#67c23a">
                  <el-radio-button label="全职"></el-radio-button>
                  <el-radio-button label="兼职"></el-radio-button>
                  <el-radio-button label="实习"></el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="职位薪资">
                <el-radio-group v-model="entJobInfo.jobPay" text-color="#fff" fill="#67c23a">
                  <el-radio-button label="面议"></el-radio-button>
                  <el-radio-button label="5-8W"></el-radio-button>
                  <el-radio-button label="8-12W"></el-radio-button>
                  <el-radio-button label="12-18W"></el-radio-button>
                  <el-radio-button label="18-20W"></el-radio-button>
                  <el-radio-button label="20W以上"></el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="具体地址" prop="jobAddress">
                <el-input v-model="entJobInfo.jobAddress"></el-input>
              </el-form-item>
              <el-form-item label="工作经验">
                <el-radio-group v-model="entJobInfo.workExp" text-color="#fff" fill="#67c23a">
                  <el-radio-button label="不限"></el-radio-button>
                  <el-radio-button label="应届"></el-radio-button>
                  <el-radio-button label="1-3年"></el-radio-button>
                  <el-radio-button label="3-5年"></el-radio-button>
                  <el-radio-button label="5-8年"></el-radio-button>
                  <el-radio-button label="8年以上"></el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="学历要求">
                <el-radio-group v-model="entJobInfo.degree" text-color="#fff" fill="#67c23a">
                  <el-radio-button label="不限"></el-radio-button>
                  <el-radio-button label="大专"></el-radio-button>
                  <el-radio-button label="本科"></el-radio-button>
                  <el-radio-button label="硕士"></el-radio-button>
                  <el-radio-button label="博士"></el-radio-button>
                  <el-radio-button label="其他"></el-radio-button>
                </el-radio-group>
              </el-form-item>
              <el-form-item label="任职要求" prop="jobReq">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 4, maxRows: 4}"
                  placeholder="请输入岗位要求"
                  v-model="entJobInfo.jobReq"
                ></el-input>
              </el-form-item>
              <el-form-item label="投递邮箱" prop="email">
                <el-input v-model="entJobInfo.email"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="success" @click="funAddJob('entJobInfo')">立即发布</el-button>
                <el-button>
                  <router-link to="/">取消</router-link>
                </el-button>
              </el-form-item>
            </el-form>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import Cookies from "vue-cookies";
export default {
  name: "addJobItem",
  data() {
    return {
      key: "0000",
      options: [
        "开发",
        "维护",
        "运营",
        "市场",
        "人事",
        "财务",
        "行政",
        "后勤",
        "其他"
      ],
      entJobInfo: {
        jobType: "全职",
        jobPay: "面议",
        workExp: "不限",
        degree: "不限",
        jobAddress: "",
        email: ""
      },
      rules: {
        jobName: [
          { required: true, message: "请输入职位名称", trigger: "blur" } //非空验证
        ],
        jobDepart: [
          { required: true, message: "请输入所属部门", trigger: "blur" } //非空验证
        ],
        jobClass: [
          { required: true, message: "请选择职位类别", trigger: "blur" } //非空验证
        ],
        jobAddress: [
          { required: true, message: "请输入地址", trigger: "blur" } //非空验证
        ],
        jobReq: [
          { required: true, message: "请输入任职要求", trigger: "blur" } //非空验证
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" }, //非空验证
          {
            type: "email",
            message: "请输入正确的邮箱",
            trigger: "blur"
          }
        ]
      }
    };
  },
  computed: {},
  created() {
    window.scroll(0, 0);
    if (this.$route.query.jobid) {
      this.key = this.$route.query.jobid;

      this.axios
        .post("getEntBaseInfo", {
          entid: Cookies.get("entid"),
          key: this.key
        })
        .then(data => {
          if (data.data.status == "ok") {
            let arr = JSON.parse(data.data.data)[0];
            arr.fields.jobid = arr.pk;
            this.entJobInfo = arr.fields;
          }
        });
    } else {
      this.axios
        .post("getEntBaseInfo", {
          entid: Cookies.get("entid"),
          key: "0000"
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.entJobInfo.jobAddress = JSON.parse(
              data.data.data.entBaseInfo
            )[0].fields.entAddress;
            this.entJobInfo.email = JSON.parse(
              data.data.data.entAccountInfo
            )[0].fields.email;
          }
        });
    }
  },
  methods: {
    funAddJob(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.axios
            .post("subEntJobInfo", {
              entid: Cookies.get("entid"),
              key: this.key,
              msg: this.entJobInfo
            })
            .then(data => {
              if (data.data.status == "ok") {
                this.$router.push("/entmain");
                this.$message({
                  type: "success",
                  message: data.data.msg
                });
              }
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style scoped>
a {
  text-decoration: none;
  color: #303133;
}
.content {
  padding: 60px;
  color: #303133;
}
.content-left {
  padding-bottom: 20px;
}
.content-left span {
  font-size: 18px;
  color: #606266;
}
.content-left p {
  font-size: 14px;
  color: #909399;
}
.content-right .box-card {
  background: #f8f8f9;
}
article {
  padding: 10px;
}
</style>
